@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .header__cart {
      @media (min-width: 350px) {
        min-width: 160px;
      }
    }
  }

  &__logo {
    a {
      display: flex;
    }


    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;

      @media (max-width: 440px) {
        display: none;
      }
    }

    p {
      color: #7b7b7b;

      @media (max-width: 650px) {
        display: none;
      }
    }
  }

  .button--cart {
    @media (max-width: 350px) {
      min-width: 0;

      span {
        display: none;
      }
      .button__delimiter {
        display: none;
      }
    }

  }
}
